.promo {
    margin-top: -60px;
    height: calc(100vh - 70px);
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-self: start;
    text-align: center;
    color: var(--color-text);

    &__title {
        font-size: calc(20px + 10 * (100vw / 1440));
        font-weight: 600;
        animation: reveal ease-out 1s;
        opacity: 0;
        animation-fill-mode: forwards;
    }

    &__subtitle {
        font-size: calc(25px + 25 * (100vw / 1440));
        margin-top: 15px;
        font-weight: 600;
        animation: reveal ease 1s 0.15s;
        opacity: 0;
        animation-fill-mode: forwards;


    }

    &__descr {
        margin-top: 30px;
        font-size: 14px;
        line-height: 1.5;
        animation: reveal ease 1s 0.3s;
        opacity: 0;
        animation-fill-mode: forwards;
    }

    &__typing {
        margin-left: 1%;
        margin-right: 1%;
        color: #4a8f78;
    }
}

@keyframes reveal {
    0% {
        transform: scale(1.2);
        opacity: 0
    }

    100% {
        transform: scale(1);
        opacity: 1
    }
}
